import React from 'react';
import PropTypes from 'prop-types';
import styled, { css } from 'styled-components';
import { color, typography, spacing, paper } from './../shared/styles';
import { inlineGlow } from './../shared/animation';

// prettier-ignore
const Illustration = styled.div`
  height: 3rem;
  width: 3rem;
  margin-right: 20px;

  > * {
    display: block;
    height: 100%;
    width: auto;
  }
`;

const MetaInner = styled.div``;

const Meta = styled.div`
  font-size: ${typography.size.s2}px;
  line-height: 20px;
`;

const Actions = styled.div``;

const SectionUpper = styled.div`
  padding: ${spacing.padding.large}px;
  display: flex;

  ${Meta} {
    flex: 1;
    ${MetaInner} {
      max-width: 800px;
    }
  }

  ${Illustration}, ${Actions} {
    flex: 0 1 auto;
  }

  ${Actions} {
    text-align: right;
    margin-left: 20px;
  }
`;

const SectionLower = styled.div`border-top: 1px solid ${color.mediumlight};`;

// prettier-ignore
const SectionWrapper = styled.section`
  ${paper};
  color: ${color.darkest};

  position: relative;
  overflow: hidden;



  ${props => props.loading && css`
    ${Illustration} {
      ${inlineGlow};
    }
    ${Meta} {
      &:before, &:after {
        content: '';
        display: block;
        ${inlineGlow};
      }

      &:before {
        height: 24px;
        width: 50%;
        margin-bottom: 8px;
      }

      &:after {
        height: 16px;
        width: 75%;
      }
    }
  `}
`;

export default function Section({ loading, illustration, meta, actions, children, ...props }) {
  if (loading) {
    return (
      <SectionWrapper loading={loading} {...props}>
        <SectionUpper>
          <Illustration />
          <Meta />
        </SectionUpper>
      </SectionWrapper>
    );
  }
  return (
    <SectionWrapper {...props}>
      <SectionUpper>
        {illustration && <Illustration>{illustration}</Illustration>}
        {meta && (
          <Meta>
            <MetaInner>{meta}</MetaInner>
          </Meta>
        )}
        {actions && <Actions>{actions}</Actions>}
      </SectionUpper>

      {children && <SectionLower>{children}</SectionLower>}
    </SectionWrapper>
  );
}

Section.propTypes = {
  loading: PropTypes.bool,
  illustration: PropTypes.node,
  meta: PropTypes.node,
  actions: PropTypes.node,
  children: PropTypes.node,
};
Section.defaultProps = {
  loading: false,
  illustration: null,
  meta: null,
  actions: null,
  children: null,
};



