import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { color, typography } from '../shared/styles';
import Icon from './Icon';
import Link from './Link';
import Subheading from './../composition/Subheading';

const Left = styled.div``;
const Title = styled.div``;
const Right = styled.div``;

const TitleHeading = styled.span`
  font-weight: ${typography.weight.extrabold};
  color: ${color.dark};
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  line-height: 32px;
`;

const TitleSubheading = styled(Subheading)`
  font-size: 10px;
  color: #8c9baa;
  float: left;
`;

const Header = styled.div`
  /* Layout */
  display: flex;
  position: relative;

  ${Title} {
    flex: 1;
  }

  ${Left}, ${Right} {
    flex: 0 1 auto;
  }

  ${Right} {
    text-align: right;
    margin-left: 20px;
  }

  /* Styling */
  font-size: ${typography.size.s1}px;
  transition: all 150ms ease-out;
  line-height: 18px;
  padding: 7px 15px;
  color: ${color.darker};
  text-decoration: none;

  svg {
    height: 12px;
    width: 12px;
    margin: 3px 0;
    vertical-align: top;
  }
`;

const Back = styled(Link)`
  position: relative;
  z-index: 1;
`;

export default function ListHeading({ left, title, right, secondary, secondaryOnClick, ...props }) {
  return (
    <Header {...props}>
      {secondary && (
        <Left>
          <Back icon primary onClick={secondaryOnClick}>
            <Icon icon="arrowleft" />
          </Back>
        </Left>
      )}
      {title && (
        <Title>
          {secondary ? (
            <TitleHeading>{title}</TitleHeading>
          ) : (
            <TitleSubheading>{title}</TitleSubheading>
          )}
        </Title>
      )}

      {right && <Right>{right}</Right>}
    </Header>
  );
}

ListHeading.propTypes = {
  left: PropTypes.node,
  title: PropTypes.node,
  right: PropTypes.node,
  secondary: PropTypes.bool,
  secondaryOnClick: PropTypes.func,
};

ListHeading.defaultProps = {
  left: null,
  title: null,
  right: null,
  secondary: false,
  secondaryOnClick: () => 0,
};



