import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import ListItem from './../basics/ListItem';

// prettier-ignore
const List = styled.div`
  min-width: 180px;
`;

function TooltipLinkList({ links }) {
  return (
    <List>
      {links.map(({ title, href, onClick, active }) => (
        <ListItem key={title} title={title} href={href} onClick={onClick} active={active} />
      ))}
    </List>
  );
}

TooltipLinkList.propTypes = {
  links: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
      href: PropTypes.string,
      onClick: PropTypes.func,
      active: PropTypes.bool,
    }).isRequired
  ).isRequired,
};

export default TooltipLinkList;



